<template>
  <div class="home-container">
    <!-- 顶部导航栏 -->
    <el-header class="header">
      <div class="logo">记账平台</div>
      <div class="user-info">
        <span>欢迎，{{ $store.state.user?.name || '管理员' }}</span>
        <el-button type="text" @click="handleLogout">退出登录</el-button>
      </div>
    </el-header>
    <div class="main-content">
      <!-- 侧边栏导航 -->
      <el-aside class="sidebar">
        <el-menu
          :default-active="activeMenu"
          class="el-menu-vertical"
          router
          @select="handleSelect"
        >
          <el-submenu index="1">
            <template #title>
              <i class="el-icon-s-home"></i>
              <span>系统管理</span>
            </template>
            <el-menu-item index="/home/enterprise-init">企业初始化</el-menu-item>
              <el-menu-item index="/home/accounting-subject">会计科目管理</el-menu-item>
